* {
  font-family: "Georama", sans-serif;
  font-size: 16px;
}
body,
html,
#root {
  margin: 0;
  height: 100%;
}

.App {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.pages-container {
  display: flex;
  width: calc(100% - 2em);
  margin: auto;
  align-items: center;
  margin-top: 1em
}

@media (min-width: 480px) {
  .App {
    display: grid;
    grid-template-columns: 1fr 3fr;
    column-gap: 1em;
    height: 100%;
  }
}
