.header {
  background-color: #074c42;
}

.profile {
  display: flex;
  justify-content: center;
  padding-top: 1em;
  flex-direction: column;
  align-items: center;
  color: white;
}

.profile > img {
  width: 50%;
  max-width: 100px;
  border-radius: 100%;
}

.profile > span {
  font-size: 24px;
  padding: 0.5em;
  text-align: center;
}

.profile .contact {
  padding-bottom: 1em;
}
.profile .contact .contact-item {
  display: flex;
  align-items: center;
}
.profile .contact .contact-item span {
  padding-left: 0.5em;
}
.profile .contact .contact-item span a {
  color: white;
}
@media (min-width: 480px) {
  .header {
    height: 100%;
  }
}
